@import url('https://fonts.googleapis.com/css?family=Kaushan+Script|Montserrat:400,700&amp;subset=cyrillic-ext');


.header {
    width: 100%;
    padding-top:3px;
  
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
  }
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header__logo {
    font-size: 1em;
    font-weight: 30;
    color: #fff;
  }
  

  /*  nav  */
.nav {
    font-style: 14px;
    text-transform: uppercase;
  }
  .nav__link {
    display: inline-block;
    vertical-align: top;
    margin: 0 15px;
    position: relative;
  
    color: #fff;
    text-decoration: none;
  
    transition: color 0.1s linear;
  }
  .nav__link:after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
  
    background-color: #fce38a;
    opacity: 0;
  
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
  
    transition: opacity 0.1s linear;
  }
  
  .nav__link:hover {
    color: #fce38a;
  }
  .nav__link:hover:after,
  .nav__link.active:after {
    opacity: 1;
  }
  .nav__link.active {
    color: #fce38a;
  }