.promo{
    margin-bottom: 63px;
    background: linear-gradient(315deg, rgb(5, 175, 90) 3%, rgb(29, 184, 132) 38%, rgb(25, 154, 145) 68%, rgb(7, 122, 42) 98%);
    animation: gradient 15s ease infinite;
    background-size: 400% 400%;
    background-attachment: fixed;
   
}

/* intro */
.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100vh;
  }
  .intro__inner {
    color: #fff;
    width: 100%;
    max-width: 880px;
    margin: 0 auto;
  
    text-align: center;
  }
  .intro__title {
    font-size: 4em;
    font-weight: 700;
    text-transform: uppercase;
  
    line-height: 1;
  }
  .intro__title:after {
    content: "";
    display: block;
    width: 60px;
    height: 3px;
    margin: 60px auto;
  
    background-color: #fff;
  }
  
  .intro__suptitle {
    margin-bottom: 20px;
    font-family: "Kaushan Script";
    font-size: 60px;
    color: #fff;
  }
  
  
  .slider {
    width: 100%;
    
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
  }
  
  .slider__inner {
    display: flex;
    justify-content: space-between;
  }
  .slider__item {
    width: 23%;
    padding: 20px 0;
    position: relative;
  
    border-top: 5px solid #fff;
    opacity: 0.7;
  
    font-size: 18px;
    color: #fff;
    text-transform: uppercase;
  }
  .slider__item.active {
    opacity: 1;
  }
  .slider__item.active::before {
    content: "";
    display: block;
    width: 70px;
    height: 5px;
  
    background-color: #f38181;
  
    position: absolute;
    top: -5px;
    left: 0;
    z-index: 1;
  }
  
  .slider__num {
    font-size: 24px;
    font-weight: 700;
  }
  
  /* WAVE */
  
  @keyframes gradient {
    0% {
        background-position: 0% 0%;
    }
    50% {
        background-position: 100% 100%;
    }
    100% {
        background-position: 0% 0%;
    }
  }
  
  .wave {
    background: rgb(255 255 255 / 25%);
    border-radius: 1000% 1000% 0 0;
    position: fixed;
    width: 200%;
    height: 12em;
    animation: wave 10s -3s linear infinite;
    transform: translate3d(0, 0, 0);
    opacity: 0.8;
    bottom: 0;
    left: 0;
    z-index: -1;
  }
  
  .wave:nth-of-type(2) {
    bottom: -1.25em;
    animation: wave 18s linear reverse infinite;
    opacity: 0.8;
  }
  
  .wave:nth-of-type(3) {
    bottom: -2.5em;
    animation: wave 20s -1s reverse infinite;
    opacity: 0.9;
  }
  
  @keyframes wave {
    2% {
        transform: translateX(1);
    }
  
    25% {
        transform: translateX(-25%);
    }
  
    50% {
        transform: translateX(-50%);
    }
  
    75% {
        transform: translateX(-25%);
    }
  
    100% {
        transform: translateX(1);
    }
  }
  
  