
.block1{
    font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom, #1e3c72, #2a5298);
  color: #fff;
  overflow-x: hidden;
}
  .main {
    text-align: center;
    padding: 20px;
  }
  .section {
    margin: 30px 0;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }
  .carousel {
    display: flex;
    overflow-x: auto;
    gap: 20px;
    padding: 10px;
  }
  .carousel img {
    max-width: 300px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }
  .button {
    background: #ff9800;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    border-radius: 5px;
    transition: background 0.3s;
  }
  .button:hover {
    background: #e68900;
  }
  
  .hidden {
    display: none;
  }
  